import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
import uniPopup from '@/components/uni-popup/uni-popup.vue'
import cuPanel from '@/components/custom/cu-panel.vue'
import cuCell from '@/components/custom/cu-cell.vue'
import cuCellGroup from '@/components/custom/cu-cell-group.vue'
export default {
	components: {
		uniSearchBar,
		uniPopup,
		cuPanel,
		cuCell,
		cuCellGroup
	},
	data() {
		return {
			name: '',
			title: '产品分类',
			datas: null,
			searchDatas: null,
			addTypeName: '',
			editTypeName: ''
		}
	},
	onLoad(options) {
		this.name = options.name
	},
	onShow() {
		this.datas = uni.getStorageSync('productType')
		this.datas = this.datas.filter((item) => {
			return item.name !== '所有分类'
		})
		this.searchDatas = this.datas
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleOpenAdd() {
			this.addTypeName = ''
			this.$nextTick(function(){
				this.$refs.add.open()
			})
		},
		handleOpenEdit(val) {
			this.editTypeName = val.name
			this.$nextTick(function(){
				this.$refs.edit.open()
			})
		},
		handleAdd() {
			if (this.addTypeName) {
				this.datas.push({ name: this.addTypeName })
				uni.setStorageSync('productType', this.datas)
				this.searchDatas = this.datas.filter((item) => {
					return item.name !== '所有分类'
				})
				this.addTypeName = ''
			}
			this.$nextTick(function(){
				this.showAddModal = false
			})
		},
		handleEdit(val) {				
			if (this.editTypeName) {
				for (let item of this.datas) {
					if (item.name == val.name) {
						item.name = this.editTypeName
					}
				}
				uni.setStorageSync('productType', this.datas)
				this.searchDatas = this.datas.filter((item) => {
					return item.name !== '所有分类'
				})
				this.editTypeName = ''
			}
			this.$nextTick(function(){
				this.showEditModal = false
			})
		},
		handleSearch(val) {
			if (val.value) {
				this.searchDatas = this.datas.filter((item) => {
					return item.name.indexOf(val.value) !== -1
				})
			} else {
				this.searchDatas = this.datas
			}
		}
	}
}
